/*Global Style*/
/*----------------------------------------------------*/
.archx-section-title {
  padding-bottom: 50px;
  .title-serial {
    line-height: 1;
    font-size: 80px;
    font-weight: 700;
    padding-bottom: 15px;
    display: inline-block;
    color: var(--main-color-2);
    font-family: var(--heading-2);
  }
  h2 {
    color: #000000;
    font-size: 38px;
    font-weight: 500;
    line-height: 50px;
    span {
      font-weight: 400;
      position: relative;
      display: inline-block;
      color: var(--main-color-2);
      &:before {
        left: 0;
        bottom: 3px;
        content: "";
        height: 1px;
        width: 100%;
        position: absolute;
        background-color: var(--main-color-2);
      }
    }
  }
}
/*Header Section 3*/
/*----------------------------------------------------*/
.archx-header-section {
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  padding-top: 20px;
  position: absolute;
  .container {
    max-width: 1505px;
  }
  .navbar-nav {
    display: inherit;
  }
  .dropdown {
    position: relative;
    &:hover {
      .dropdown-menu {
        visibility: visible;
        opacity: 1;
        clip: inherit;
        -webkit-transform: scaleY(1);
        -khtml-transform: scaleY(1);
        -moz-transform: scaleY(1);
        -ms-transform: scaleY(1);
        -o-transform: scaleY(1);
        transform: scaleY(1);
      }
    }
  }
  .dropdown-menu {
    left: 0;
    top: 60px;
    z-index: 100;
    margin: 0px;
    padding: 20px 0;
    height: auto;
    min-width: 250px;
    display: block;
    border: none;
    border-radius: 0;
    position: absolute;
    opacity: 0;
    box-shadow: 0 0 20px rgb(0 0 0 / 15%);
    transform: scaleY(0);
    transform-origin: 0 0 0;
    -webkit-transform-origin: 0 0 0;
    -ms-transform-origin: 0 0 0;
    -o-transform-origin: 0 0 0;
    text-align: left;
    transition: all 0.3s ease-in-out;
    .dropdown-menu {
      top: 30px;
      left: 250px;
      transform: scaleY(0);
      opacity: 0 !important;
      a {
        font-size: 12px;
        font-weight: 600;
        color: #1a2428;
        &:before {
          display: none;
        }
      }
    }
    li {
      display: block;
      margin: 0 !important;
      transition: 0.3s all ease-in-out;
      &:last-child {
        border-bottom: none;
      }
      &:after {
        display: none;
      }
      &:hover {
        .dropdown-menu {
          top: 0;
          opacity: 1 !important;
        }
      }
    }
    a {
      width: 100%;
      display: block;
      position: relative;
      padding: 10px 30px !important;
      color: #1a2428 !important;
      font-weight: 600 !important;
      font-size: 14px !important;
      &:before {
        display: none;
      }
      &:hover {
        color: var(--main-color-2) !important;
      }
    }
  }
  .main-navigation {
    li {
      margin: 0px 30px;
      position: relative;
      &:after {
        top: 15px;
        width: 5px;
        height: 5px;
        content: "";
        right: -10px;
        position: absolute;
        transform: scale(0);
        border-radius: 100%;
        transition: 500ms all ease;
        background-color: var(--main-color-2);
      }
      a {
        font-weight: 500;
        color: var(--black-color);
        padding-bottom: var(--padding-bottom-35);
      }
      &:hover {
        &:after {
          transform: scale(1);
        }
        a {
          color: var(--main-color-2);
        }
      }
    }
  }
  .header-top-content {
    margin-bottom: 20px;
    .header-top-cta {
      li {
        color: #fff;
        font-size: 15px;
        position: relative;
        margin-right: 30px;
        &:before {
          top: 3px;
          width: 1px;
          content: "";
          right: -18px;
          height: 18px;
          position: absolute;
          background-color: #e5e5e530;
        }
        &:last-child {
          &:before {
            display: none;
          }
        }
        i {
          margin-right: 10px;
          color: var(--main-color-2);
        }
      }
    }
    .brand-logo {
      top: 0;
      left: 0;
      right: 0;
      width: 115px;
      margin: 0 auto;
      position: absolute;
    }
    .header-social {
      margin-left: 15px;
      position: relative;
      &:before {
        top: 3px;
        width: 1px;
        content: "";
        left: -5px;
        height: 18px;
        position: absolute;
        background-color: #e5e5e530;
      }
      a {
        color: #fff;
        margin-left: 15px;
        &:hover {
          color: var(--main-color-2);
        }
      }
    }
    .header-language-select {
      select {
        color: #fff;
        border: none;
        background-color: transparent;
        option {
          color: var(--main-color-2);
        }
      }
    }
  }
  .header-main-menu-wrapper {
    z-index: 1;
    padding: 18px 15px;
    background-color: #fff;
    &:before {
      left: 0;
      width: 0;
      top: 0px;
      height: 0;
      z-index: -1;
      content: "";
      position: absolute;
      border-right: 100px solid transparent;
      border-bottom: 92px solid var(--main-color-2);
    }
  }
  .archx-side-bar-button {
    cursor: pointer;
    padding-left: 20px;
    margin-right: 25px;
    span {
      height: 2px;
      display: block;
      margin: 5px 0px;
      transition: 500ms all ease;
      background-color: #000000;
      &:nth-child(1) {
        width: 14px;
      }
      &:nth-child(2) {
        width: 22px;
      }
      &:nth-child(3) {
        width: 18px;
      }
    }
    &:hover {
      span {
        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3) {
          width: 22px;
        }
      }
    }
  }
  .header-cta-number {
    line-height: 1;
    .header-cta-icon {
      width: 40px;
      height: 40px;
      margin-right: 15px;
      border-radius: 100%;
      background-color: #f5f5f5;
      i {
        color: var(--black-color);
      }
    }
    .ar-title {
      font-size: 13px;
      font-weight: 500;
    }
    .ar-value {
      display: block;
      color: #dd1d26;
      font-size: 14px;
      font-family: var(--heading-2);
    }
  }
  .header-cta-btn {
    margin-left: 30px;
    a {
      color: #fff;
      width: 155px;
      height: 55px;
      font-size: 14px;
      font-weight: 500;
      background-color: var(--main-color-2);
      i {
        margin-left: 8px;
        transition: 500ms all ease;
      }
      &:hover {
        background-color: #000;
        i {
          margin-left: 10px;
        }
      }
    }
  }
}
.archx-header-section.sticky_on {
  top: 0;
  padding: 0;
  width: 100%;
  z-index: 20;
  position: fixed;
  animation-duration: 0.7s;
  animation-fill-mode: both;
  animation-name: fadeInDown;
  animation-timing-function: ease;
  transition: 0.3s all ease-in-out;
  background-color: var(--white-color);
  box-shadow: 0px 0px 18px 1px rgba(0, 0, 0, 0.1);
  .header-top-content {
    display: none !important;
  }
  .header-main-menu-wrapper:before {
    display: none;
  }
  .archx-side-bar-button {
    padding-left: 0;
  }
}
.archx-header-section.archx-header-style-two {
  padding-top: 0;
  background-color: #fff;
  .header-main-menu-wrapper {
    padding: 20px 85px;
  }
  .header-main-menu-wrapper:before {
    display: none;
  }
  .archx-side-bar-button {
    padding-left: 0;
  }
}
/*Slider Section*/
/*---------------------------------------------------*/
