}
.archx-slider-counter {
  z-index: 1;
  .ar-slider-shape-img {
    left: 0px;
    top: -80px;
    z-index: -1;
    opacity: 0.2;
  }
  .archx-slider-counter-text {
    max-width: 480px;
    h2 {
      line-height: 1;
      font-size: 200px;
      font-weight: 700;
      color: var(--main-color-2);
    }
    p {
      color: #fff;
      font-size: 18px;
      font-family: var(--heading-2);
    }
