}
.archx-slider-content {
  &:before {
    left: 0;
    right: 0;
    top: -85px;
    width: 1px;
    content: "";
    height: 530px;
    margin: 0 auto;
    position: absolute;
    background-color: var(--main-color-2);
  }
}
.archx-slider-text {
  padding-left: 80px;
  .slider-slug {
    color: #fff;
    font-size: 14px;
    font-weight: 500;
    padding: 3px 25px;
    margin-bottom: 25px;
    display: inline-block;
    transform: skewX(-20deg);
    background-color: var(--main-color-2);
  }
  h1 {
    color: #fff;
    font-size: 44px;
    font-weight: 700;
    line-height: 55px;
    padding-bottom: 20px;
  }
  p {
    color: #fff;
    padding-bottom: 30px;
  }
}
.archx-video-play-btn {
  .archx-slider-btn {
    margin-right: 25px;
    a {
      color: #fff;
      width: 165px;
      height: 55px;
      background-color: var(--main-color-2);
      i {
        margin-left: 5px;
        transition: 500ms all ease;
      }
      &:hover {
        color: #000;
        background-color: #fff;
        i {
          margin-left: 10px;
        }
      }
    }
  }
  .archx-video-play-btn {
    a {
      color: #fff;
      width: 60px;
      height: 60px;
      border-radius: 100%;
      border: 1px solid #fff;
    }
  }
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
