/*Faq Section*/
/*----------------------------------------------------*/
.archx-faq-section {
  z-index: 1;
  overflow: hidden;
  padding: 90px 0px 70px;
  .archx-section-title {
    max-width: 730px;
  }
  .ar-bg {
    right: 0;
    bottom: 0;
    z-index: -1;
  }
}
.archx-faq-content-wrapper {
  .accordion-item {
    border: none;
    margin-bottom: 10px;
    border-bottom: 1px solid #d9d9d9;
  }
  .accordion-item:first-of-type .accordion-button {
    border-radius: 0;
  }
  .accordion-button:not(.collapsed) {
    background-color: transparent;
  }
  .accordion-item:first-of-type {
    border: none;
    border-radius: 0;
    border-bottom: 1px solid #d9d9d9;
  }
  .accordion-button {
    padding: 0;
    border: none;
    border: none;
    height: 50px;
    color: #000000;
    font-size: 18px;
    font-weight: 500;
    &:focus {
      outline: none;
      box-shadow: none;
    }
  }
  .accordion-body {
    padding: 0;
    color: #00000080;
    line-height: 42px;
  }
  .accordion-button:not(.collapsed)::after,
  .accordion-button::after {
    width: auto;
    height: auto;
    background-image: none;
    content: "\f067";
    font-weight: 900;
    font-family: "Font Awesome 5 Pro";
  }
  .accordion-button:not(.collapsed)::after {
    content: "\f068";
  }
}
.archx-faq-section-2 {
  z-index: 1;
  padding: 120px 0px 90px;
  &:before {
    top: 0;
    right: 0;
    z-index: -1;
    width: 53%;
    content: "";
    height: 100%;
    position: absolute;
    background-color: #f7f4f3;
  }
}
.archx-faq-accordion-2 {
  .archx-faq-content-wrapper .accordion-item:first-of-type,
  .archx-faq-content-wrapper .accordion-item,
  .accordion-item:last-of-type .accordion-button.collapsed,
  .archx-faq-content-wrapper .accordion-item:first-of-type .accordion-button {
    border: none;
    background-color: #f7f4f3;
  }
  .accordion-button:not(.collapsed) {
    box-shadow: none;
  }
}
.archx-faq-pointer-wrapper {
  top: 0;
  left: 0;
  width: 47%;
  height: 100%;
  background-color: #fcfcfc;
  .archx-faq-exp {
    top: 80px;
    left: 25px;
    width: 140px;
    height: 120px;
    padding-top: 25px;
    background-color: #000;
    h3 {
      color: #fff;
      line-height: 0.75;
      font-size: 50px;
      font-weight: 700;
    }
    p {
      color: #fff;
      font-size: 12px;
    }
  }
}
.archx-point-area {
  top: 10%;
  right: 10%;
  position: absolute;
  .archx-pointer-indicatior {
    top: 0;
    right: 0;
    width: 45px;
    height: 45px;
    cursor: pointer;
    position: absolute;
    border-radius: 100%;
    background-color: #fff;
    transition: 500ms all ease;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    &:hover {
      color: #fff;
      transform: rotate(45deg);
      background-color: var(--main-color-2);
    }
  }
  .archx-pointer-text {
    top: -25px;
    opacity: 0;
    right: 40px;
    width: 240px;
    padding: 20px;
    position: absolute;
    background-color: #fff;
    transition: 500ms all ease;
    box-shadow: 0px 4px 4px 0px #00000040;
    &:before {
      width: 0;
      height: 0;
      top: 40px;
      content: "";
      right: -20px;
      position: absolute;
      border-left: 20px solid #d3d1d1;
      border-top: 10px solid transparent;
      border-bottom: 10px solid transparent;
    }
    h3 {
      color: #000000;
      font-size: 14px;
      font-weight: 700;
      padding-bottom: 10px;
    }
    p {
      font-size: 12px;
      line-height: 19px;
    }
  }
  &:hover {
    .archx-pointer-text {
      opacity: 1;
      right: 80px;
    }
  }
  &:nth-child(2) {
    top: 20%;
    right: 50%;
  }
  &:nth-child(3) {
    top: auto;
    right: 15%;
    bottom: 20%;
  }
  &:nth-child(4) {
    top: auto;
    right: 30%;
    bottom: 50%;
  }
  &:nth-child(5) {
    top: auto;
    left: 40%;
    right: auto;
    bottom: 30%;
  }
}
